import "../scss/sections/_header.scss";
import {
  Route,
  Switch,
  BrowserRouter as Router,
  Redirect,
} from "react-router-dom";

import { NavLink } from "react-router-dom";

import Hero from "./Hero";
import About from "./About";
import Contact from "./Contact";
import Resume from "./Resume";
import Projects from "./Projects";
import Data from "./data/headerData";
import logo from "../images/bernhardt-trans.png";
import { BiMenuAltRight } from "react-icons/bi";
import { AiOutlineClose } from "react-icons/ai";
import { useState } from "react";

const Header = () => {
  const [open, setOpen] = useState(false);
  return (
    <>
      <Router>
        <header>
          <div className="inner-header content-standard">
            <div className="logo-header">
              <a href="/react-portfolio">
                <img src={logo} alt="didnt find" />
              </a>
            </div>
            {Data.map((data) => (
              <ul key={data.id}>
                <li>
                  <NavLink
                    activeClassName="navbar__link--active"
                    exact
                    to={data.adress}
                  >
                    {data.title}
                  </NavLink>
                </li>
              </ul>
            ))}
          </div>
          <div className="mb-container">
            <div className="mb-menu">
              <div onClick={() => setOpen(!open)}>
                {" "}
                {open ? <AiOutlineClose /> : <BiMenuAltRight />}
              </div>
            </div>
            {open ? (
              <div className="mb-menu-dropdown">
                {Data.map((data) => (
                  <ul key={data.id}>
                    <li>
                      <NavLink
                        activeClassName="navbar__link--active"
                        exact
                        to={data.adress}
                        onClick={() => setOpen(!open)}
                      >
                        {data.title}
                      </NavLink>
                    </li>
                  </ul>
                ))}
              </div>
            ) : null}
          </div>
        </header>
        <Switch>
          <Route exact path="/">
            <Redirect to="/react-portfolio/" />
          </Route>
          <Route exact path="/react-portfolio/">
            <Hero />
          </Route>
          <Route path="/react-portfolio/about">
            <About />
          </Route>
          <Route path="/react-portfolio/contact">
            <Contact />
          </Route>
          <Route path="/react-portfolio/resume">
            <Resume />
          </Route>
          <Route path="/react-portfolio/projects">
            <Projects />
          </Route>
        </Switch>
      </Router>
    </>
  );
};

export default Header;
